

.home{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
}
.home_details{
    /* background: white padding-box; 
	box-shadow: 3px 4px 15px #0000001A;
	border-radius: 5px;
	border-color: #013243;  */
	width: 20%;
    color: #525c7b;
    margin-left: 60px;
    margin-top: 100px;
    height: 100%;
    
}
.home_detail{
    margin-left: 30px;
}

.home_detail h3{
    font-weight: bold;    font-size: 22px;
}
.home_detail h2{
    font-weight: bold;
    color:black;
    font-size: 37px;
}
.home_detail button{
    background-color: darkcyan;
    width: 150px;
    height: 50px;
    font-weight: bold;
    font-size: 17px;
    border-radius: 50px;
    
}
.home_detail button:hover{
    background-color:white;
    color: darkcyan;
    border-color: darkcyan;
}

.myphoto img{
    border-radius: 100px;
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    width: 200px;
    height: 200px;
    box-shadow: 0 10px 6px -6px #777; 
}

.links img{
    width: 40px;
    height: 50px;
    margin-right: 15px;
    margin-bottom: 0;
    background-color:white;
}

